import React from 'react';
import Gist from 'react-gist';
import { SVGPath, Command } from '@dead-simple/svg-path-jsx';
const renderedCanvas = props => {
  if (props === undefined) return <></>;
  return (
    <SVGPath
      container={{
        width: 250,
        height: 250,
        viewBox: '0 0 100 100',
      }}
      showPoints={!props.renderMode && props.showPoints ? props.showPoints : undefined}
      pathFragments={!props.renderMode && props.pathFragments ? props.pathFragments : undefined}
    >
      <Command.M x={25} y={25} color='cornflowerblue' />
      <Command.C c1={[25, 75]} c2={[50, 75]} e={[50, 50]} color='forestgreen' />
      <Command.S c2={[75, 25]} e={[75, 75]} color='crimson' />
    </SVGPath>
  );
};

export default function Example5({ renderMode, showPoints, pathFragments, controls, setSelected }) {
  return (
    <div
      className='demo-card'
      onClick={() => {
        setSelected({
          render: props => (
            <>
              {renderedCanvas(props)}
              <div>
                {props.controls.render(props)}
                <Gist
                  id={
                    props.renderMode
                      ? '6bfabaccb43c8d3b530b7aa8b5ae5c18'
                      : '85b5f51c844dd8a995e4fc14d5719bd6'
                  }
                />
              </div>
            </>
          ),
        });
      }}
    >
      {renderedCanvas({ renderMode, showPoints, pathFragments, controls })}
      <code className='demo-card-summary'>
        <div>{renderMode ? `<path d={\`` : `<SVGPath>`}</div>
        <div style={{ color: 'cornflowerblue' }}>{renderMode ? `Mâ€¦` : `<Command.M â€¦ />`}</div>
        <div style={{ color: 'forestgreen' }}>{renderMode ? `Câ€¦` : `<Command.C â€¦ />`}</div>
        <div style={{ color: 'crimson' }}>{renderMode ? `Sâ€¦` : ` <Command.S â€¦ />`}</div>
        <div>{renderMode ? `\`} />` : `</SVGPath>`}</div>
      </code>
    </div>
  );
}
